import React from 'react';
import axios from 'axios';

function getFilterPayload(start, end, aoiFeature, collections) {
  const filterPayload = {
    op: 'and',
    args: [
      {
        op: 't_intersects',
        args: [
          { property: 'datetime' },
          {
            interval: [
              userTzDate2utcString(startOfDay(start)),
              userTzDate2utcString(endOfDay(end))
            ]
          }
        ]
      },
      {
        op: 's_intersects',
        args: [{ property: 'geometry' }, aoiFeature.geometry]
      },
      {
        op: 'in',
        args: [{ property: 'collection' }, collections]
      }
    ]
  };
  return filterPayload;
}

